.slider {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 18px;
  transition: 0.3s;
  pointer-events: none;
}

.slider::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 6px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: var(--background-tertiary-inverse);
  transition: 0.3s;
}

