  width: var(--prose);
  display: inline-block;
  background: var(--color-distinct);
  mask-image: url("/assets/svg/icons/read-me.svg");
  mask-mode: alpha;
  mask-repeat: no-repeat;
  mask-size: var(--prose);
  mask-position: center;
  margin-inline-end: var(--rhythm-presto);
  margin-inline-start: var(--rhythm-presto);
}

.case-details:hover::before, .case-details:active::before {
  background: var(--color-prominent);
}

@media screen and (min-width: 40em) {
  .project-overview .overview {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    grid-template-areas: "summary summary aside";
    column-gap: var(--rhythm-andante);
  }
  .project-overview .overview .summary {
    grid-area: summary;
  }
  .project-overview .overview aside {
    grid-area: aside;
    padding-block-start: 0;
  }
  section:has(.objectives, .results) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: var(--rhythm-moderato);
    max-inline-size: none;
    width: 100%;
  }
  .project-overview .cta {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    align-items: baseline;
  }
}
/* DETAIL PAGES */
.date-header {
  font-size: var(--hed-quaternary);
  padding-block-start: var(--rhythm-andante);
  margin-block-start: var(--rhythm-andante);
  border-top: var(--border-thin) solid var(--border-color-subtle);
}

.links box-l {
  padding: 0;
}
.links box-l h3 {
  margin-block-start: var(--rhythm-moderato);
}

.day-group:has(article:nth-of-type(2)) article {
  padding-block: var(--rhythm-moderato);
}

article:is(.review) {
  max-inline-size: none;
  display: flex;
  flex-flow: column;
  gap: var(--rhythm-vivace);
  background: var(--background-primary);
  border: 1px solid var(--border-color-nuance);
  --flow-space: var(--rhythm-andantino);
}
article:is(.review) .content {
  padding: var(--rhythm-moderato);
}
article:is(.review) .lede {
  font-size: var(--hed-tertiary);
  color: var(--color-distinct);
}
article:is(.review) .byline {
  padding-block-start: var(--rhythm-moderato);
}
article:is(.review) .reviewer {
  font-size: var(--hed-quaternary);
  color: var(--color-distinct);
}
article:is(.review) .reviewer + p {
  padding-block-start: var(--rhythm-prestissimo);
  color: var(--color-nuance);
}
article:is(.review) .role {
  font-weight: var(--weight-medium);
}
article:is(.review) .context {
  font-style: italic;
}
article:is(.review) .relationship {
  text-transform: capitalize;
}

@media only screen and (min-width: 40em) {
  article:is(.review) {
    flex-flow: row;
    column-gap: var(--rhythm-vivace);
    grid-row-gap: var(--rhythm-vivace);
    width: fit-content;
  }
  article:is(.review) img {
    object-fit: cover;
    object-position: center;
    aspect-ratio: 1/1;
    max-width: 250px;
  }
  article:is(.review) > div {
    display: flex;
    flex-flow: column;
    max-inline-size: var(--measure);
  }
}
.splash h2 {
  font-size: var(--hed-primary);
}
.splash h1 {
  padding-block-start: var(--rhythm-largo);
  font-size: var(--hed-secondary);
  font-family: var(--font-sans);
  color: var(--color-distinct);
}
.splash p {
  padding-block-start: var(--rhythm-andante);
  max-width: 50ch;
}
.splash .cta {
  display: inline-grid;
}
.splash .cluster {
  flex-direction: row;
  align-items: flex-start;
  padding-block-start: var(--rhythm-andante);
}
.splash .cluster a {
  flex-grow: 1;
}

.codex .border-thickness {
  display: flex;
  flex-direction: column;
  gap: var(--rhythm-moderato);
}
.codex .border-thickness div {
  height: var(--rhythm-largo);
  width: 100%;
  border-color: var(--color-distinct);
  border-style: solid;
}

